*, :before, :after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

body {
  font-family: Roboto, sans-serif;
  font-size: 16px;
  background-color: #ffc699;
}

.root {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.App {
  max-width: 600px;
  width: 100%;
  margin: 1em auto;
  background-color: #fff1e5;
}

.App .Accordion {
  -webkit-box-shadow: 0 0 5px 3px #807873;
          box-shadow: 0 0 5px 3px #807873;
  padding: 1em;
}

.App .Accordion .AccordionItem {
  margin-bottom: 1em;
}

.App .Accordion .AccordionItem .Accordion-title {
  background-color: #CC9E7A;
  position: relative;
}

.App .Accordion .AccordionItem .Accordion-title .Accordion-title__item {
  color: #FFF1E5;
  padding: 0.5em;
}

.App .Accordion .AccordionItem .Accordion-title .Accordion-title__item:hover {
  cursor: pointer;
}

.App .Accordion .AccordionItem .Accordion-title span {
  position: absolute;
  top: 20px;
  right: 1em;
  width: 10px;
  height: 2px;
  background-color: #333333;
}

.App .Accordion .AccordionItem .Accordion-title span::before {
  content: "";
  background-color: #333333;
  display: block;
  width: 2px;
  height: 10px;
  position: relative;
  top: -4px;
  left: 4px;
}

.App .Accordion .AccordionItem .Accordion-content {
  visibility: hidden;
  height: 0;
  opacity: 0;
}

.App .Accordion .AccordionItem:last-child {
  margin-bottom: 0;
}

.Accordion-content.active {
  visibility: visible !important;
  padding: 1em .5em .5em 1.5em;
  opacity: 1 !important;
  height: 40px !important;
}

.Accordion-content.active p {
  color: #807873;
}

.active-span::before {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}
